.table {
    width: 55%;
    text-align: center;
    border-collapse: collapse;
    border: 2px solid blue;
    margin-top: 2rem;
}

.table > thead {
    background-color: #f5f5f5;
    font-weight: bold;
}

.table td, .table th {
    border: 1px solid #a0a0a0;
    padding: var(--padding-mid);
}